const INFO_KEY = 'yy-shopping_info'
const HISTORY_KEY = 'yy_history_list'
// 获取个人信息
export const getInfo = () => {
  const res = localStorage.getItem(INFO_KEY)
  return res
    ? JSON.parse(res)
    : {
        token: '',
        userId: ''
      }
}
// 设置个人信息
export const setInfo = (info) => {
  localStorage.setItem(INFO_KEY, JSON.stringify(info))
}

// 移除个人呢信息
export const removeInfo = () => {
  localStorage.removeItem(INFO_KEY)
}

// 获取搜索数组
export const getsouSuo = () => {
  const res = localStorage.getItem(HISTORY_KEY)
  return res ? JSON.parse(res) : []
}
// 设置搜索数组
export const SetsouSuo = (z) => {
  localStorage.setItem(HISTORY_KEY, JSON.stringify(z))
}
