import { getInfo, setInfo } from '@/utils/storage'

export default {
  namespaced: true, // 开启命名空间
  state () {
    return {
      userInfo: getInfo()
    }
  },
  getters: {
  },
  mutations: {
    setUserInfo (state, obj) {
      state.userInfo = obj
      setInfo(obj)
    }
  },
  actions: {
    logout (context) {
      context.commit('setUserInfo', {})
      context.commit('cart/setCarList', [], { root: true })
    }
  },
  modules: {
  }

}
