import { getCartList, getShuLiang, delGouWuChe } from '@/api/cart'
import { Toast } from 'vant'

export default {
  namespaced: true,
  state () {
    return {
      cartList: []
    }
  },
  mutations: {
    setCarList (state, n) {
      state.cartList = n
    },
    danXuan (state, goodsId) {
      const goods = state.cartList.find(item => item.goods_id === goodsId)
      goods.isChecked = !goods.isChecked
    },
    quanXuan (state, n) {
      state.cartList.forEach(item => {
        item.isChecked = n
      })
    },
    changeCount (state, { goodsId, goodsNum }) {
      const goods = state.cartList.find(item => item.goods_id === goodsId)
      goods.goods_num = goodsNum
    }

  },
  actions: {
    async getCartAction (context) {
      const { data } = await getCartList()

      data.list.forEach(item => {
        item.isChecked = true
      })
      context.commit('setCarList', data.list)
    },
    // goodsNum, goodsId, goodsSkuId
    async shuangPinGengXin (context, obj) {
      const { goodsNum, goodsId, goodsSkuId } = obj

      context.commit('changeCount', { goodsId, goodsNum })

      await getShuLiang(goodsId, goodsNum, goodsSkuId)
    },

    // 删除购物车数据
    async delGouWuChe (context) {
      const selCartList = context.getters.selCartList
      const id = selCartList.map(item => item.id)
      await delGouWuChe(id)
      Toast('删除成功')

      context.dispatch('getCartAction')
    }
  },
  getters: {
    // 求所有的商品累加总数
    cartTotal (state) {
      return state.cartList.reduce((sum, item) => sum + item.goods_num, 0)
    },
    // 选中的商品项
    selCartList (state) {
      return state.cartList.filter(item => item.isChecked)
    },
    // 选中的总数
    selCount (state, getters) {
      return getters.selCartList.reduce((sum, item) => sum + item.goods_num, 0)
    },
    // 选中的总价
    selPrice (state, getters) {
      return getters.selCartList.reduce((sum, item) => {
        return sum + item.goods_num * item.goods.goods_price_min
      }, 0).toFixed(2)
    },

    // 是否全选
    isAllChecked (state) {
      return state.cartList.every(item => item.isChecked)
    }
  }
}
