<template>
  <div class="login">
    <van-nav-bar title="会员登录" left-arrow @click-left="$router.go(-1)" />
    <div class="container">
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>

      <div class="form">
        <div class="form-item">
          <input v-model="mobile" class="inp" maxlength="11" placeholder="请输入手机号码" type="text">
        </div>
        <div class="form-item">
          <input v-model="picValue" class="inp" maxlength="5" placeholder="请输入图形验证码" type="text">
          <img v-if="picUel" :src="picUel" @click="getPicCode" alt="">
        </div>
        <div class="form-item">
          <input v-model="smsCode" class="inp" placeholder="请输入短信验证码" type="text">
          <button @click="getCode">
            {{ second === zongMiaoShu ? '获取验证码' : second + '秒后重新发送' }}
          </button>
        </div>
      </div>

      <div @click="dengLu" class="login-btn">登录</div>
    </div>
  </div>
</template>

<script>
import { getPicCode, getMsgCode, shouJiCode } from '@/api/login'
export default {
  name: 'LoginIndex',
  data () {
    return {
      picKey: '',
      picUel: '',
      zongMiaoShu: 60,
      second: 60,
      timer: null,
      mobile: '', // 手机号
      picValue: '', // 图形验证码
      smsCode: ''
    }
  },
  created () {
    this.getPicCode()
  },
  methods: {

    // 获取图片验证码
    async getPicCode () {
      const { data: { base64, key } } = await getPicCode()
      this.picUel = base64
      this.picKey = key

      // 轻提示 获取验证码成功
      // this.$toast('获取成功')
    },

    // 验证
    validFn () {
      if (!/^1[3-9]\d{9}$/.test(this.mobile)) {
        this.$toast('请输入正确的手机号')
        return false
      }
      if (!/^\w{4}$/.test(this.picValue)) {
        this.$toast('请输入正确的图形验证码')
        return false
      }
      return true
    },
    // 倒计时
    async getCode () {
      if (!this.validFn()) {
        return
      }
      if (!this.timer && this.zongMiaoShu === this.second) {
        // 发送请求，获取验证码
        await getMsgCode(this.picValue, this.picKey, this.mobile)
        this.$toast('发送成功，请注意查收')
        // console.log(res)

        this.timer = setInterval(() => {
          this.second--

          if (this.second <= 0) {
            clearInterval(this.timer)
            this.timer = null
            this.second = this.zongMiaoShu
          }
        }, 1000)
      }
    },

    async dengLu () {
      if (!this.validFn()) {
        return
      }
      if (!/^\d{6}$/.test(this.smsCode)) {
        this.$toast('请输入正确的验证码')
        return
      }

      const res = await shouJiCode(this.mobile, this.smsCode)
      console.log(res)
      this.$store.commit('user/setUserInfo', res.data)
      this.$toast('登录成功')

      const url = this.$route.query.backUrl || '/'
      this.$router.replace(url)
    }

  },
  // 离开页面销毁定时器
  destroyed () {
    clearInterval(this.timer)
  }
}
</script>

<style lang="less" scoped>
.container {
  padding: 49px 29px;

  .title {
    margin-bottom: 20px;
    h3 {
      font-size: 26px;
      font-weight: normal;
    }
    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg,#ecb53c,#ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0,0,0,.1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
