<template>
  <div class="count-box">
    <button @click="jianCount" class="jian">-</button>
    <input @change="yanZheng" :value="value" class="inp" type="text">
    <button @click="jiaCount" class="jia">+</button>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: Number,
      default: 1
    }
  },
  methods: {
    jianCount () {
      if (this.value <= 1) {
        return
      }
      this.$emit('input', this.value - 1)
    },
    jiaCount () {
      this.$emit('input', this.value + 1)
    },
    yanZheng (e) {
      const num = +e.target.value

      if (isNaN(num) || num < 1) {
        e.target.value = this.value
        return
      }

      this.$emit('input', num)
    }
  }
}
</script>

<style lang="less" scoped>
.count-box {
  width: 100px;
  display: flex;
  .jia, .jian {
    width: 30px;
    height: 30px;
    outline: none;
    border: none;
    background-color: #efefef;
  }
  .inp {
    width: 40px;
    height: 30px;
    outline: none;
    border: none;
    margin: 0 5px;
    background-color: #efefef;
    text-align: center;
  }
}
</style>
